import React from "react";
import { Route, Redirect } from "react-router-dom";
import Auth from "../../utlis/auth";
import MainLayout from "../../containers/Dashboard/MainLayout/MainLayout";

export const MainLayoutWrapper = (component) => {
  return <MainLayout>{component}</MainLayout>;
};

export const NoLayout = (component) => {
  return <>{component}</>;
};

const PrivateRoute = ({ component: Component, path, ...rest }) => {
  return (
    <Route
      {...rest}
      render={(props) =>
        Auth.isAuth()
          ? path.split("/")[2] === "login" ||
            path.split("/")[2] === "forgetPassword"
            ? MainLayoutWrapper(<Redirect to={process.env.PUBLIC_URL + "/"} />)
            : MainLayoutWrapper(<Component {...props} />)
          : path.split("/")[2] === "login" ||
            path.split("/")[2] === "forgetPassword"
          ? NoLayout(<Component {...props} />)
          : NoLayout(
              <Redirect to={{ pathname: process.env.PUBLIC_URL + "/login" }} />
            )
      }
    />
  );
};

export default PrivateRoute;
